"use client";

import { createContext, useContext, useEffect, useState } from "react";

type Comment = {
  id: string;
  text: string;
  createdAt: number;
};

type State = {
  commentsByDate: Record<string, Comment[]>;
  addComment: (date: string, text: string) => void;
};

const Ctx = createContext<State | null>(null);
const STORAGE_KEY = "a615.wod.comments";

export function WodCommentsProvider({
  children,
}: {
  children: React.ReactNode;
}) {
  const [commentsByDate, setCommentsByDate] = useState<
    Record<string, Comment[]>
  >({});

  useEffect(() => {
    const raw = localStorage.getItem(STORAGE_KEY);
    if (raw) setCommentsByDate(JSON.parse(raw));
  }, []);

  useEffect(() => {
    localStorage.setItem(STORAGE_KEY, JSON.stringify(commentsByDate));
  }, [commentsByDate]);

  const addComment = (date: string, text: string) => {
    const c: Comment = {
      id: crypto.randomUUID(),
      text,
      createdAt: Date.now(),
    };

    setCommentsByDate((prev) => ({
      ...prev,
      [date]: [...(prev[date] ?? []), c],
    }));
  };

  return (
    <Ctx.Provider value={{ commentsByDate, addComment }}>
      {children}
    </Ctx.Provider>
  );
}

export function useWodComments() {
  const ctx = useContext(Ctx);
  if (!ctx) throw new Error("useWodComments outside provider");
  return ctx;
}
