"use client";

import { createContext, useContext, useEffect, useState } from "react";

type AthleteState = {
  bookedIds: string[];
  toggleBooking: (id: string) => void;
};

const AthleteContext = createContext<AthleteState | null>(null);

const STORAGE_KEY = "a615.bookedIds";

export function AthleteProvider({ children }: { children: React.ReactNode }) {
  const [bookedIds, setBookedIds] = useState<string[]>([]);

  // cargar al iniciar
  useEffect(() => {
    const raw = localStorage.getItem(STORAGE_KEY);
    if (raw) setBookedIds(JSON.parse(raw));
  }, []);

  // persistir cambios
  useEffect(() => {
    localStorage.setItem(STORAGE_KEY, JSON.stringify(bookedIds));
  }, [bookedIds]);

  const toggleBooking = (id: string) => {
    setBookedIds((prev) =>
      prev.includes(id) ? prev.filter((x) => x !== id) : [...prev, id]
    );
  };

  return (
    <AthleteContext.Provider value={{ bookedIds, toggleBooking }}>
      {children}
    </AthleteContext.Provider>
  );
}

export function useAthlete() {
  const ctx = useContext(AthleteContext);
  if (!ctx) throw new Error("useAthlete must be used inside AthleteProvider");
  return ctx;
}
